/* General chatbot container */
.chatbot-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.chatbot-container {
  display: flex;
  width: 90%;
  max-width: 800px;
  height: 500px;
  background: #ffffff;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Left side (phone image) */
.chatbot-left {
  width: 40%;
  background: #f8f8f8;
  display: flex;
  justify-content: center;
  align-items: center;
}

.chatbot-phone-image {
  width: 80%;
  max-width: 300px;
  height: auto;
}

/* Right side (chat interface) */
.chatbot-right {
  width: 60%;
  display: flex;
  flex-direction: column;
}

/* Header */
.chatbot-header {
  background: #28a745;
  color: white;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.chatbot-header h3 {
  margin: 0;
  font-size: 18px;
}

.close-chatbot {
  background: none;
  border: none;
  color: white;
  font-size: 16px;
  cursor: pointer;
}

/* Messages */
.chatbot-messages {
  padding: 20px;
  flex: 1;
  overflow-y: auto;
}

.chatbot-message {
  margin: 10px 0;
  padding: 10px;
  border-radius: 10px;
  font-size: 14px;
}

.chatbot-message.bot {
  background: #e8f5e9;
  align-self: flex-start;
}

.chatbot-message.user {
  background: #c8e6c9;
  align-self: flex-end;
}

/* Options */
.chatbot-options {
  padding: 15px 20px;
  border-top: 1px solid #ccc;
}

.chatbot-option {
  margin: 5px;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background: #28a745;
  color: white;
  cursor: pointer;
}

.chatbot-option:hover {
  background: #1e7b33;
}

/* Floating button */
.chatbot-icon {
  position: fixed;
  bottom: 20px;
  right: 30px;
  width: 60px;
  height: 60px;
  background: #28a745;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 24px;
  z-index: 1001;
}

.close-chatbot {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 20px;
  color: #000;
  cursor: pointer;
}

.chatbot-container {
  position: relative;
}

@media (max-width: 480px) {
  .chatbot-container {
    max-width: 100%;
    border-radius: 10px;
  }

  .chatbot-message {
    font-size: 14px;
    max-width: 80%;
  }

  .chatbot-option {
    padding: 10px;
    font-size: 12px;
  }

  .chatbot-options {
    margin-bottom: 20px;
  }
}